import React from 'react';
import InfoContacts from '../components/Contacts/InfoContacts';
import SecBanner from '../components/Banner/SecBanner';
import MetaDecorator from '../components/MetaDatos/MetaDecorator';
import ReactGA from 'react-ga';
import "../components/Contacts/contacts.css";

    // Configuraci√≥n de Google Analytics
    const gaTrackingId = "UA-194698672-1"; 
    ReactGA.initialize (gaTrackingId); 
    ReactGA.pageview ("/contact");




const Contacts = () => {
    return(
        
            <div className="body_wrapper contacts" id="contacts">
                <MetaDecorator 
                        meta_title="metaTitle_Contacts"
                        meta_description="metaDescription_Contacts"
                        url="contact"
                />
                <SecBanner 
                        title_banner="title_banner_contact" 
                        descri_banner="descri_banner_contact"
                        idBanner="bannerContacts"
                        classBanner="min-vh-100 text-center"
                        paddingClass = "pr_120 pl_120" 
                        imgDisplay = "d-none"  
                />
                
                <InfoContacts
                        title = "title" name = "name" company = "company" 
                        titleServiceOpc = "titleServiceOpc"
                        email = "email"
                        subject = "subject"
                        message = "message"
                        btn = "btn"
                        ServOpc1= "ServOpc1" 
                        ServOpc2 = "ServOpc2" 
                        ServOpc3= "ServOpc3" 
                        ServOpc4= "ServOpc4"
                        ServOpc5= "ServOpc5"
                        textModal= "textModal"
                        privacyPolicy="privacyPolicy"
                        txt_privacyPolicy="txt_privacyPolicy"
                />

            </div>

    )
}
export default Contacts;