import './App.css';

import React, { Component } from 'react';
import { BrowserRouter, Route ,Switch } from "react-router-dom";
import { TranslatorProvider } from "react-translate";

/* ---- Pages ----*/
import Home from "./Pages/Home";
import About from "./Pages/About"
import Devops from "./Pages/Devops";
import Jobs from "./Pages/Jobs";
import Scrum from "./Pages/Scrum";
import Contact from "./Pages/Contact";
import Blog from "./Pages/Blog"
import Category from "./Pages/Category"
import Services from "./Pages/Services"
import NotFound from "./Pages/404";
//import Categorys from './components/Blog/Categorys';


/* ---- Menu y Footer ----*/
import Menu from './components/Header/Menu';
import Footer from "./components/Footer/Footer";
import PostSingle from "./components/Blog/PostSingle"
//import PagePost from './Pages/PagePost';
//import Layout from './components/Layout';






class App extends Component{
 	
    constructor(props) {
        super(props);
        this.state = {
            language: 'en'
        }; 
        this.languageChange = this.languageChange.bind(this);  
    }

    
      
    languageChange(event) {
        this.setState({language: event.target.value});
        localStorage.setItem("lenguaje", event.target.value);
    }


    getLayout(lang){

    const translationsLang = require('./assets/translations/'+lang+'.json');
    
    
    //ORIGINAL APP

	   return  (    
        <TranslatorProvider translations={translationsLang}>	
                <BrowserRouter > 
                    <Menu menu_blog= "menu_blog" menu_jobs= "menu_jobs" value={this.state.value} onChangeLanguage={this.languageChange} />
                        <Switch>
                            <Route exact path={"/"} component={Home} />
                            <Route path="/about" component={About} />
                            <Route path="/services" component={Services} /> 
                            <Route path="/contact" component={Contact} />    
                            <Route path="/devops" component={Devops} />                        
                            <Route path="/jobs" component={Jobs} /> 
                            <Route path="/scrum" component={Scrum} />
                            <Route path="/blog" component={Blog} /> 
                            {/* <Route path="/category" component={Category} /> */}
                            
                            <Route exact path="/post/:url" render={props => <PostSingle {...props} />} />
                            <Route path="*" component={NotFound} />         
                        </Switch>                 
                    <Footer />
                </BrowserRouter>
        </TranslatorProvider>
       )

   }

   render(){
	   return(
           this.getLayout(this.state.language)
        );
    };    
}; 


export default App;