import React, {Component} from 'react';
import { translate } from "react-translate";
import "./serviceItem.css"



const MenuEn = require('../../assets/translations/en.json');
const MenuEs = require('../../assets/translations/es.json');
let lang = null;

class ServiceItems extends Component {

    constructor(){
        super();
        this.state= {
            language: 'en'
        }
    }


    render(){

        let {¬†t , style1, styleColor, style2, style3, image} = this.props;
        
        const ActualLang = localStorage.getItem("lenguaje") ? localStorage.getItem("lenguaje") : 'en';

        if (ActualLang === 'en') {
            lang = MenuEn
          } else {
            lang = MenuEs
          } 

    return(
        <section className={`servItems  ${styleColor}`}>
            <div className="container">   
             
                <div className={` `}>
                    <div className={`row ${style2}`}>
                        <div className="col-lg-9">
                            <div className={`content-item ${style1} services_content `}>
                            <h4 className="f_600 f_size_50">{t(this.props.TitleServ)}</h4>
                            <p className="f_p f_500 ">{t(this.props.DescrServ)}</p>
                            <ul className={`f_p f_500 f_size_20 ${style3}`}>
                                {
                                    
                                    lang.ServicePage.Servitems.map((value) => {
                                        return <li>{value}</li>
                                    })    
                                }
                            </ul>
                            </div>
                        </div>
                        <div className="col-lg-3">
                            <div className="imgServ d-flex justify-content-center">
                                <img src={require ("../../img/services/" + image).default} alt=""/>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </section>


        )
    }
}

export default translate('ServicePage')(ServiceItems);